<template>
  <div class="spm">
    <cynav currIndex="2"></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;!DOCTYPE html&gt;
&lt;html&gt;
	&lt;head&gt;
		&lt;meta charset="utf-8" /&gt;
		&lt;!-- 平台与站点设置 --&gt;
		&lt;meta name="data-spm" content="3.1" /&gt;
	&lt;/head&gt;
	&lt;!-- 页面编码设置 --&gt;
	&lt;body data-spm="8888"&gt;
		&lt;!-- 按钮模块设置 --&gt;
		&lt;span data-spm="5555"&gt;点击统计&lt;/span&gt;		
		&lt;!-- 模块统计设置 --&gt;
		&lt;ul data-spm="6666" data-spm-type="all"&gt;	
            &lt;!-- 位置设置 --&gt;		
			&lt;li&gt;&lt;a data-spm="111"&gt;名称一&lt;/a&gt;&lt;/li&gt;
			&lt;li&gt;&lt;a data-spm="222"&gt;名称二&lt;/a&gt;&lt;/li&gt;
			&lt;li&gt;&lt;a data-spm="333"&gt;名称二&lt;/a&gt;&lt;/li&gt;
		&lt;/ul&gt;
		&lt;!-- 引用并初始化 --&gt;
		&lt;script src="/static/js/common/spm.js"&gt;&lt;/script&gt;
		&lt;script type="text/javascript"&gt;
			spm.push([ "_trackPageview" ]);
		&lt;/script&gt;
	&lt;/body&gt;
&lt;/html&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">spm 标签页</h2>
      <div class="apiModule" style="font-size: 14px;">
					<p><b>埋点分外部页面和内部页面：</b></p>
					<p>1、外部页面并非统计他们页面PV与UV（该功能暂不开放），而是统计该连接来源于哪个平台、页面、模块和渠道等；</p>
					<p>2、内部页面主要统计页面/栏目/平台的PV与UV；模块和按钮点击量；接口出错率等信息；</p>
					<p>3、所有页面需手调用 spm.push([<span class="atv">'_trackPageview'</span>, <span class="atn">url</span> ])方法；触发PV统计；</p>
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none"> 
&lt;!-- 页面编码设置 --&gt;
&lt;body data-spm="8888"&gt;
	&lt;script type="text/javascript"&gt;
		// 代码解发按钮统计
		spm.push([ "_trackPageview"]);
	&lt;/script&gt;
&lt;/body&gt;
</xmp >
 
      </div>

      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;!-- 按钮统计设置 --&gt;
&lt;span data-spm="5555"&gt;点击统计&lt;/span&gt;				

&lt;!-- 模块统计设置 --&gt;
&lt;ul data-spm="6666" data-spm-type="all"&gt;	
  &lt;!-- 位置设置 --&gt;		
  &lt;li&gt;&lt;a data-spm="111"&gt;名称一&lt;/a&gt;&lt;/li&gt;
  &lt;li&gt;&lt;a data-spm="222"&gt;名称二&lt;/a&gt;&lt;/li&gt;
  &lt;li&gt;&lt;a data-spm="333"&gt;名称二&lt;/a&gt;&lt;/li&gt;
&lt;/ul&gt;
  
&lt;script type="text/javascript"&gt;
  // 代码解发按钮统计
  spm.push([ "_trackEvent", "5555"]);
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">外投连接统计</h2>				
            <ul class="ul-props">
              <li>
                <p>在外投URL后面直接加参数：spm=站点.平台.页面.模块.位置.0.渠道；</p>
                <p>如：xxxx.html<a>?spm=8.1.666.22.1.0.02000000</a>；</p>
              </li>
              <li>
                <p>只统计渠道，可在URL后页面直接加参数：channel_source=渠道；</p>
                <p>如：xxxx.html<a>?channel_source=02000000</a>；</p>
              </li>
            </ul>
      </div>
      <div class="apiModule">
        <h2 class="cy_h2">畅由页面埋点</h2>				
            <ul class="ul-props">
              <li>
                <p><a>平台.站点标记</a>：在meta标签加属性name="data-spm"，属性 content="平台.站点"；不添加默认 3.1</p>
                <p>示例：<span class="prettyprint">&lt;meta <span class="atn">name</span><span class="pun">=</span><span class="atv">"data-spm"</span> <span class="atn">content</span><span class="pun">=</span><span class="atv">"3.1"</span> /&gt;</span></p>
                <span class="isNeed">必须：<b class="atn">Y</b></span>
              </li>
              <li>
                <p><a>页面标记</a>：在body标签加属性 data-spm="页面code"</p>
                <p>示例：<span class="prettyprint">&lt;body <span class="atn">data-spm</span><span class="pun">=</span><span class="atv">"201"</span> &gt;</span></p>
                <span class="isNeed">必须：<b class="atn">Y</b></span>
              </li>
              <li>
                <p><a>模块统计埋点</a>：在统计模块标签加属性 data-spm="模块code"</p>
                <p>示例：<span class="prettyprint">&lt;ul <span class="atn">data-spm</span><span class="pun">=</span><span class="atv">"111"</span> &gt;</span></p>
                <span class="isNeed">必须：<b class="atn">N</b></span>
              </li>
              <li>
                <p><a>模块子模块统计埋点</a>：在模块标签内部元素上加上属性 data-spm="标记如ID"</p>
                <p>示例：<span class="prettyprint">&lt;ul <span class="atn">data-spm</span><span class="pun">=</span><span class="atv">"111"</span> &gt;</span><span class="prettyprint">&lt;li <span class="atn">data-spm</span><span class="pun">=</span><span class="atv">"1323"</span>&gt;</span></p>
                <span class="isNeed">必须：<b class="atn">N</b></span>
              </li>
              <li>
                <p><a>按钮统计埋点</a>：在统计模块标签加属性 data-spm="模块code"</p>
                <p>示例：<span class="prettyprint">&lt;span <span class="atn">data-spm</span><span class="pun">=</span><span class="atv">"111"</span> &gt;</span></p>
                <span class="isNeed">必须：<b class="atn">N</b></span>
              </li>
              <li>
                <p><a>接口统计埋点</a>：接口统计调用的是模块统计；接口路径作为模块code，事件为02/click，接口返回的code作为描述信息；</p>
                <p>示例：spm.push([ <span class="atv">'_trackEvent'</span>, <span class="atv">'/butler/cmcc/busy'</span>, <span class="atv">'02'</span>, <span class="atn">_data.code</span> ]);</p>
                <span class="isNeed">必须：<b class="atn">N</b></span>
              </li>
              <li>
                <p><a>栏目或平台标记</a>：只要在URL后面加上column参数即可；统计到从该连接开始访问的PV、UV、模块和按钮等信息</p>
                <p>示例：xxxx.html<a>?column=666</a></p>
                <span class="isNeed">必须：<b class="atn">N</b></span>
              </li>
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {trim} from "../../assets/js/codeStyle";
export default {
  name: 'spm',
  data () {
    return {
      msg: 'spm',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .prettyprint {padding: 0px 10px;}
  .isNeed {color: #009f5d;position: absolute;right: 30px;top: 15px;}
  .isNeed b{font-weight: normal;}
  .cy_example .codeArea{height:515px}
  .cy_example .iframeBox{position:absolute;top:562px;width:100%;bottom:0}
</style>



